:root {
  --dl-color-gray-500: #595959;
  --dl-color-gray-700: #999999;
  --dl-color-gray-900: #D9D9D9;
  --dl-size-size-large: 144px;
  --dl-size-size-small: 48px;
  --dl-color-danger-300: #A22020;
  --dl-color-danger-500: #BF2626;
  --dl-color-danger-700: #E14747;
  --dl-color-gray-black: #000000;
  --dl-color-gray-white: #FFFFFF;
  --dl-size-size-medium: 96px;
  --dl-size-size-xlarge: 192px;
  --dl-size-size-xsmall: 16px;
  --dl-space-space-unit: 16px;
  --dl-color-primary-100: #003EB3;
  --dl-color-primary-300: #0074F0;
  --dl-color-primary-500: #14A9FF;
  --dl-color-primary-700: #85DCFF;
  --dl-color-success-300: #199033;
  --dl-color-success-500: #32A94C;
  --dl-color-success-700: #4CC366;
  --dl-size-size-xxlarge: 288px;
  --dl-size-size-maxwidth: 1400px;
  --dl-color-default-white: rgba(255, 255, 255, 1);
  --dl-radius-radius-round: 50%;
  --dl-color-default-white1: rgba(255, 255, 255, 1);
  --dl-color-default-white2: rgba(255, 255, 255, 1);
  --dl-color-default-white3: rgba(255, 255, 255, 1);
  --dl-color-default-white4: rgba(255, 255, 255, 1);
  --dl-color-default-white5: rgba(255, 255, 255, 1);
  --dl-color-default-white6: rgba(255, 255, 255, 1);
  --dl-space-space-halfunit: 8px;
  --dl-space-space-sixunits: 96px;
  --dl-space-space-twounits: 32px;
  --dl-color-default-saffron: rgba(254, 200, 85, 1);
  --dl-radius-radius-radius2: 2px;
  --dl-radius-radius-radius4: 4px;
  --dl-radius-radius-radius8: 8px;
  --dl-space-space-fiveunits: 80px;
  --dl-space-space-fourunits: 64px;
  --dl-color-default-saffron1: rgba(254, 200, 85, 1);
  --dl-color-default-saffron2: rgba(254, 200, 85, 1);
  --dl-color-default-saffron3: rgba(254, 200, 85, 1);
  --dl-color-default-saffron4: rgba(254, 200, 85, 1);
  --dl-color-default-saffron5: rgba(254, 200, 85, 1);
  --dl-color-default-saffron6: rgba(254, 200, 85, 1);
  --dl-color-default-tangblue: rgba(32, 95, 216, 1);
  --dl-space-space-threeunits: 48px;
  --dl-color-default-tangblue1: rgba(32, 95, 216, 1);
  --dl-color-default-tangblue2: rgba(32, 95, 216, 1);
  --dl-color-default-tangblue3: rgba(32, 95, 216, 1);
  --dl-color-default-tangblue4: rgba(32, 95, 216, 1);
  --dl-color-default-tangblue5: rgba(32, 95, 216, 1);
  --dl-color-default-tangblue6: rgba(32, 95, 216, 1);
  --dl-color-default-frenchgray: rgba(168, 170, 176, 1);
  --dl-color-default-frenchgray1: rgba(168, 170, 176, 1);
  --dl-color-default-frenchgray2: rgba(168, 170, 176, 1);
  --dl-color-default-frenchgray3: rgba(168, 170, 176, 1);
  --dl-color-default-frenchgray4: rgba(168, 170, 176, 1);
  --dl-color-default-raisinblack: rgba(20, 24, 41, 1);
  --dl-color-default-raisinblack1: rgba(20, 24, 41, 1);
  --dl-color-default-raisinblack2: rgba(20, 24, 41, 1);
  --dl-color-default-raisinblack3: rgba(20, 24, 41, 1);
  --dl-color-default-raisinblack4: rgba(20, 24, 41, 1);
  --dl-color-default-raisinblack5: rgba(20, 24, 41, 1);
  --dl-color-default-raisinblack6: rgba(20, 24, 41, 1);
  --dl-space-space-oneandhalfunits: 24px;
}
.button {
  color: var(--dl-color-gray-black);
  display: inline-block;
  padding: 0.5rem 1rem;
  border-color: var(--dl-color-gray-black);
  border-width: 1px;
  border-radius: 4px;
  background-color: var(--dl-color-gray-white);
}
.input {
  color: var(--dl-color-gray-black);
  cursor: auto;
  padding: 0.5rem 1rem;
  border-color: var(--dl-color-gray-black);
  border-width: 1px;
  border-radius: 4px;
  background-color: var(--dl-color-gray-white);
}
.textarea {
  color: var(--dl-color-gray-black);
  cursor: auto;
  padding: 0.5rem;
  border-color: var(--dl-color-gray-black);
  border-width: 1px;
  border-radius: 4px;
  background-color: var(--dl-color-gray-white);
}
.list {
  width: 100%;
  margin: 1em 0px 1em 0px;
  display: block;
  padding: 0px 0px 0px 1.5rem;
  list-style-type: none;
  list-style-position: outside;
}
.list-item {
  display: list-item;
}
.teleport-show {
  display: flex !important;
  transform: none !important;
}
.Heading {
  font-size: 32px;
  font-family: Inter;
  font-weight: 700;
  line-height: 1.15;
  text-transform: none;
  text-decoration: none;
}
.Content {
  font-size: 16px;
  font-family: Inter;
  font-weight: 400;
  line-height: 1.15;
  text-transform: none;
  text-decoration: none;
}
